// Section Picture

.section.gallery {
  background-image: linear-gradient($traditionColor 6%, $primaryColor 0%);

  @include media-size(min-tablet) {
    background-image: linear-gradient($traditionColor 12%, $primaryColor 0%);
  }

  @include media-size(min-desktop) {
    background-image: linear-gradient($traditionColor 17%, $primaryColor 0%);
  }
}

.gallery-picture {
  // position: relative;

  background-image: url(../img/gallery/gallery2x@mob1x.png);
  width: 100%;
  height: 122px;
  margin-bottom: 80px;

  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../img/gallery/gallery2x@mob2x.png);
  }

  @include media-size(min-tablet) {
    background-image: url(../img/gallery/gallery2x@tablet1x.png);
    margin-bottom: 121px;
    height: 264px;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../img/gallery/gallery2x@tablet2x.png);
    }
  }

  @include media-size(min-desktop) {
    background-image: url(../img/gallery/gallery1xdesktop.png);
    margin-bottom: 123px;
    height: 430px;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../img/gallery/gallery2xdesktop.png);
    }
  }
}

// Section Gallery
.section.gallery {
  // padding-top: 400px;
  padding-bottom: 80px;
  padding-left: 15px;
  padding-right: 15px;
  color: $secondaryColor;
}

.gallery-title {
  @include visuallyHidden;
}

// gallery-list
.gallery-list__item {
  margin-bottom: 30px;

  @include media-size(min-tablet) {
    margin-bottom: 35px;
  }
}

.gallery-list__picture {
  width: 100%;
  height: 85px;

  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 30px;
  background-size: contain;

  @include media-size(min-tablet) {
    margin-bottom: 19px;
  }

  @include media-size(min-desktop) {
    margin-bottom: 15px;
  }
}

.gallery-list__picture.person1 {
  height: 85px;

  background-image: url(../img/gallery/person1-85@1x.png);

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../img/gallery/person1-170@2x.png);
  }
}

.gallery-list__picture.person2 {
  background-image: url(../img/gallery/person2-85@1x.png);

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../img/gallery/person2-170@2x.png);
  }
}

.gallery-list__picture.person3 {
  background-image: url(../img/gallery/person3-85@1x.png);

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../img/gallery/person3-170@2x.png);
  }
}

.gallery-list__wrap {
  // position: relative;
  z-index: 10;
  padding: 43px 66px 26px 57px;
  margin-bottom: 30px;

  font-weight: 500;
  font-size: 12px;
  line-height: 2.58;
  text-align: center;
  background-color: #fafafa;

  @include media-size(min-tablet) {
    margin-bottom: 24px;
    padding: 47px 80px 34px 80px;

    font-size: 14px;
    line-height: 2.14;
    letter-spacing: 0.04em;
  }

  @include media-size(min-desktop) {
    margin-bottom: 15px;
    padding: 47px 89px 39px 87px;

    font-size: 16px;
    line-height: 1.87;
  }

  &::before {
    content: ' ';
    position: absolute;
    width: 100%;
    height: 40px;

    top: 28px;
    left: 61px;
    background-image: url(../img/gallery/text.svg);
    background-repeat: no-repeat;
    z-index: -1;

    @include media-size(min-tablet) {
      top: 30px;
      left: 46px;
    }

    @include media-size(min-desktop) {
      top: 32px;
      left: 54px;
    }
  }
}

.gallery-list__text {
  background-image: repeating-linear-gradient(
    #e1e1e1 56px,
    #e1e1e1 57px,
    #fafafa 57px,
    #fafafa 86px
  );

  @include media-size(min-tablet) {
    background-image: repeating-linear-gradient(
      #e1e1e1 56px,
      #e1e1e1 57px,
      #fafafa 57px,
      #fafafa 85px
    );
  }
}

.gallery_svg-ellipse {
  fill: $titleColor;
  text-align: center;
  margin-bottom: 18px;

  @include media-size(min-tablet) {
    margin-bottom: 14px;
  }

  @include media-size(min-desktop) {
    margin-bottom: 15px;
  }
}

.gallery-list__name {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.85;
  text-align: center;
  color: $secondaryColor;

  @include media-size(min-tablet) {
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 1.87;
  }

  @include media-size(min-desktop) {
    margin-bottom: 35px;

    font-size: 20px;
    line-height: 1.85;
  }
}

//  JS slider
.slick-dots {
  display: flex;
  list-style: none;
  justify-content: center;
  padding: 0;
}

.slick-dots {
  margin: 0;
  font-size: 0;
}

.slick-dots button {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  border: none;
  color: red;
}

.slick-dots li {
  &:not(:last-child) {
    margin-right: 18px;
  }
}

li.slick-active button {
  background: transparent;
  background-image: url(../img/gallery/home-1.svg);
  background-position: top 0;
  border: none;
  border-radius: 0;
  background-position: center;
  background-size: 100%;
}
